.product-card {
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 16px;
    margin: 16px;
    text-align: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s;
  }
  
  .product-card:hover {
    transform: scale(1.05);
  }
  
  .product-card img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
  }
  
  .product-card h3 {
    font-size: 1.5em;
    margin: 16px 0;
  }
  
  .product-card p {
    font-size: 1.2em;
    color: #333;
  }
  
  .quantity-controls {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 16px 0;
  }
  
  .quantity-controls button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 5px 10px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1em;
  }
  
  .quantity-controls button:hover {
    background-color: #0056b3;
  }
  
  .quantity-controls span {
    margin: 0 10px;
    font-size: 1.2em;
  }
  
  .btn {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1em;
  }
  
  .btn:hover {
    background-color: #0056b3;
  }